@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  width: 100vw;
  display: flex;
  flex-direction: column;
  font-family: "Lato", sans-serif;
}

.header-container-general {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.header-autogestion {
  height: 50px;
  border-bottom-left-radius: 2rem;
  background-color: #eeeeee;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-autogestion a {
  color: #666666;
  transition: all 0.2s ease;
}

.header-autogestion a:hover {
  color: #3f3f3f;
}

.login-trips-help {
  display: flex;
}

.login-trips-help span {
  display: none;
}

.header-autogestion-item {
  margin-right: 0.5rem;
}

.header-navigation {
  min-height: 90px;
  display: flex;
  align-items: center;
}

.header-navigation .fas,
.header-navigation .far {
  width: 50px;
  height: 50px;
  font-size: 1rem;
  padding: 1rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #270570;
}

.header-navigation .fas:hover,
.header-navigation .far:hover {
  transition: all 0.2s ease;
  border: 2px solid #270570;
}

.header-navigation .fas:active,
.header-navigation .far:active {
  background-color: #270570;
  color: #fff;
}

.header-navigation .button-circle-label:hover {
  transition: all 0.2s ease;
}

.header-navigation .button-circle-label:active {
  background-color: #270570;
  color: #fff;
}

.header-product-item {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  min-width: 90px;
}

.header-list-products {
  padding: 0 0.5rem;
  display: flex;
  overflow-x: scroll;
}

.header-corners-container {
  width: 100vw;
}

.header-navigation {
  width: 100vw;
  overflow-x: scroll;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.header-logo {
  padding: 0.8rem;
  font-size: 1.8rem;
  font-weight: bold;
}

.header-autogestion-link {
  display: flex;
  align-items: baseline;
  justify-content: center;
  font-weight: bold;
}

.header-autogestion-link span {
  margin-left: 0.5rem;
}

.header-autogestion-link .fas,
.header-autogestion-link .far {
  font-size: 1.2rem;
  padding: 0.5rem 0rem 0.5rem 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-autogestion-container {
  display: flex;
}

.shifu-inspiration-button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.shifu-inspiration-button .far {
  padding: 0;
  min-width: 50px;
  height: 50px;
}

.shifu-3-button-circle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.button-circle-label {
  color: #270570;
}

.button-circle-label:hover {
  transition: all 0.2s ease;
}

.button-circle-label:active {
  background-color: #270570;
  color: #fff;
}

.header-autogestion-link {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #270570;
}

/* Media Queries */
@media all and (min-width: 768px) {
  .header-navigation {
    min-height: 90px;
    display: flex;
    align-items: center;
  }
  .header-navigation .fas,
  .header-navigation .far {
    width: 50px;
    height: 50px;
    font-size: 1rem;
    padding: 1rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
  }
  .header-navigation .fas:hover,
  .header-navigation .far:hover {
    transition: all 0.2s ease;
    border: 2px solid #270570;
  }
  .header-navigation .fas:active,
  .header-navigation .far:active {
    background-color: #270570;
    color: #fff;
  }
}

@media all and (min-width: 480px) {
  .login-trips-help {
    display: flex;
  }
  .login-trips-help span {
    display: flex;
  }
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: 0;
}

html {
    line-height: 1.15;
    -webkit-text-size-adjust: 100%;
}

body {
    margin: 0;
}

main {
    display: block;
}

h1 {
    font-size: 2em;
    margin: 0.67em 0;
}

hr {
    box-sizing: content-box;
    height: 0;
    overflow: visible;
}

pre {
    font-family: monospace, monospace;
    font-size: 1em;
}

a {
    background-color: transparent;
}

abbr[title] {
    border-bottom: none;
    text-decoration: underline;
    -webkit-text-decoration: underline dotted;
            text-decoration: underline dotted;
}

b,
strong {
    font-weight: bolder;
}

code,
kbd,
samp {
    font-family: monospace, monospace;
    font-size: 1em;
}

small {
    font-size: 80%;
}

sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}

sub {
    bottom: -0.25em;
}

sup {
    top: -0.5em;
}

img {
    border-style: none;
}

button,
input,
optgroup,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}

button,
input {
    overflow: visible;
}

button,
select {
    text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
    -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
}

fieldset {
    padding: 0.35em 0.75em 0.625em;
}

legend {
    box-sizing: border-box;
    color: inherit;
    display: table;
    max-width: 100%;
    padding: 0;
    white-space: normal;
}

progress {
    vertical-align: baseline;
}

textarea {
    overflow: auto;
}

[type="checkbox"],
[type="radio"] {
    box-sizing: border-box;
    padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
    height: auto;
}

[type="search"] {
    -webkit-appearance: textfield;
    outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit;
}

details {
    display: block;
}

summary {
    display: list-item;
}

template {
    display: none;
}

[hidden] {
    display: none;
}

